import React, { useState, useEffect } from "react"
import InputBar from "../components/InputBar"
import MenuBar from "../components/MenuBar"
import "../styles/style.css"
import ThemeProvider from "@material-ui/core/styles/MuiThemeProvider"
import darkTheme from "../theme/dark"
import lightTheme from "../theme/light"
import CssBaseline from "@material-ui/core/CssBaseline"
import MessageList from "../components/MessageList"

const global = {}

const IndexPage = () => {
  const [theme, setTheme] = useState(darkTheme)
  const [identity, setIdentity] = useState("Anonymous")
  const [room, setRoom] = useState("main")
  const [language, setLanguage] = useState("en")
  const [messages, setMessages] = useState([
    { identity: "Bob asdf", message: "I like trains" },
  ])
  const toggleThemeHandler = () => {
    if (theme == darkTheme) setTheme(lightTheme)
    else setTheme(darkTheme)
  }

  global.room = room
  const watchRoom = async () => {
    const response = await fetch("/watch?room=" + encodeURI(global.room))
    if (response.status === 200) {
      const data = await response.json()
      const { identity, message } = data
      if (identity && message) {
        const newMessages = messages.concat([{ identity, message }])
        setMessages(newMessages)
      }
    }
    await watchRoom()
  }

  useEffect(watchRoom, [])

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline></CssBaseline>
      <MenuBar
        toggleThemeHandler={toggleThemeHandler}
        identity={identity}
        setIdentity={setIdentity}
        room={room}
        setRoom={setRoom}
        language={language}
        setLanguage={setLanguage}
      ></MenuBar>
      <MessageList messages={messages} language={language}></MessageList>
      <InputBar identity={identity}></InputBar>
    </ThemeProvider>
  )
}

export default IndexPage
