import React, { useState } from "react"
import { makeStyles } from "@material-ui/core/styles"
import Paper from "@material-ui/core/Paper"
import InputBase from "@material-ui/core/InputBase"
import IconButton from "@material-ui/core/IconButton"
import SendIcon from "@material-ui/icons/Send"

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    alignItems: "center",
    bottom: 10,
    left: 10,
    right: 10,
    position: "fixed",
    paddingLeft: 5,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
}))

export default ({ identity, room }) => {
  const classes = useStyles()
  const [message, setMessage] = useState("")
  const handleSumit = async e => {
    e.preventDefault()
    const response = await fetch("/send?room=" + encodeURI(room), {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ message, identity }),
    })
    const data = await response.json()
    if (data.success) setMessage("")
  }
  return (
    <Paper
      component="form"
      className={classes.root}
      onSubmitCapture={handleSumit}
    >
      <InputBase
        className={classes.input}
        value={message}
        onChange={e => setMessage(e.target.value)}
        placeholder={`Send somthing in this chat as ${identity}`}
      />
      <IconButton className={classes.iconButton} onClick={handleSumit}>
        <SendIcon />
      </IconButton>
    </Paper>
  )
}
